* {
	box-sizing: border-box;
}

img {
	max-width: 50%;
	height: 100%;
	margin-bottom: 1em;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: #191919;
	color: #e1e1e1;
	font-size: 1.1rem;
	line-height: 1.6;
	width: 100%;
	letter-spacing: 0.5px;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.main {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
}

nav {
	width: 100%;
	background-color: #252525;
	color: #e1e1e1;
	margin-bottom: 3em;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

input {
	margin-right: 1em;
}

h1 {
	margin-right: 3em;
}

.nav-container {
	width: 50%;
	margin: 0 auto;
}

.nav-el {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.search-box,
.search-box-small {
	outline: none;
	background-color: #707070;
	color: white;
	padding: 1em 5em;
	/* margin-left: auto; */
	border-radius: 20px;
	border: none;
	font-size: 1.1rem;
}

.search-box-small {
	padding: 1em 2em;
}

a {
	color: orange;
}

::placeholder {
	color: white;
	margin: 0;
	font-size: 1.1rem;
}

.btn {
	padding: 0.75em 1.5em;
	margin: 0;
	background-color: #e1e1e1;
	border-radius: 10px;
	border: none;
	font-size: 1.1rem;
	font-weight: bold;
}

.article-container {
	width: 50%;
}

.article-summary {
	display: flex;
	flex-direction: row;
}

.article-description {
	margin: 1em;
}
.articles {
	margin-bottom: 5em;
	border-bottom: 2px solid #e1e1e1;
}
